import loginSheet from "../../components/loginSheet";
import shopItem from "../../components/shopItem";
import callUs from "../../components/callUs";
import { getLocation, getSetting } from "../../libs/auth.js";
import { scan } from "../../libs/utils";
import { get,post } from "../../libs/request.js"
export default {
  data() {
    return {
	  markers: [],
      addressInfo: null,
      userInfo: null,
      shopItemData: {},
      showSheet: false,
      showShopItem: false,
      showCallUs: false
    };
  },
  onLoad() {
    this.checkAuth();
  },
  components: {
    loginSheet,
    shopItem,
    callUs
  },
  methods: {
    async checkAuth() {
      let that = this;
      this.userInfo = uni.getStorageSync("user_info") || null;
      this.addressInfo = uni.getStorageSync("user_address") || null;
	  if(this.addressInfo){
		  that.getNearDevice({
		  	lat: that.addressInfo['latitude'],
		  	lng: that.addressInfo['longitude']
		  })
	  }
      if (!this.userInfo) {
        that.showSheet = true;
      }
      if (!this.addressInfo) {
        that.addressInfo = await getLocation();
		that.getNearDevice({
			lat: that.addressInfo['latitude'],
			lng: that.addressInfo['longitude']
		})
      }
    },
    authSuccess(e) {
      this.userInfo = uni.getStorageSync("user_info") || null;
      this.showSheet = false;
    },
    makertap(e) {
		let id = e.markerId;
		let sn = this.markers[id]['title'];
		post('driver/shop/info',{
			sn:sn
		}).then(res=>{
			if(res.code === 200){
				let data = res.data;
				this.shopItemData = data;
			}
			console.log(res)
		})
		  this.showShopItem = true;
    },
    goLocation(e) {
      // 回到自己的位置
      let content = uni.createMapContext("map", this);
      content.moveToLocation();
    },
    callMe() {
      // 联系客服
      this.showCallUs = true;
    },
    async scanCode() {
      // 扫码
      let data = await scan();
	  console.log(data);
	  if(data.result){
		  // 扫码成功后跳转
		  uni.navigateTo({
		    url: "loan"
		  });
	  }
    },
    hideShop() {
      this.showShopItem = false;
    },
    regionchange(e) {
		let that = this;
		if(e.type === 'end' && e.causedBy === 'drag'){
			let content = uni.createMapContext("map", this);
			content.getCenterLocation({
				success(res) {
					that.addressInfo['latitude'] = res['latitude'];
					that.addressInfo['longitude'] = res['longitude'];
					that.getNearDevice({
						// lat: that.addressInfo['latitude'],
						// lng: that.addressInfo['longitude']
						lat:29.587185,
						lng:106.569563
					});
					console.log('拖拽后的经纬度：'+res)
				},
				fail(err){
					uni.showToast({
						title:"获取定位失败",
						icon:"none"
					})
				}
			})
		}
    },
    goNearShop() {
		let latitude = this.addressInfo.latitude
		let longitude = this.addressInfo.longitude
      // 附件门店跳转
      uni.navigateTo({
        url: `nearShop?latitude=${latitude}&longitude=${longitude}`,
      });
    },
	goUser() {
		// 跳转个人界面
		uni.navigateTo({
		  url: "userInfo"
		});
	},
	goShopDetail(e){
		// 商户详情
		uni.navigateTo({
		  url: "shopDetail"
		});
	},
	getNearDevice(data){
		// 获取附件的设备
		let that = this;
		post('driver/nearby',data).then(res=>{
			if(res.code === 200){
				let data = res.data;
				if(data.length > 0){
					that.markers = [];
					data.map((item,index)=>{
						that.markers.push({
							id: index,
							iconPath: "http://wd-qidian.oss-cn-beijing.aliyuncs.com/mini/icon-maker.png",
							width: 30,
							height: 30,
							title: item['driverNum'],
							latitude: item['lat'],
							longitude: item['lng'],
						})
					})
				}
			}
		})
	}
  }
};
